  computed: {

  }
}
</script>
<style scoped>
.input-json {
  height: 100%;
  display: block;
}
.val-class {
  color: #606266;
  font-family: monospace;
  border: 1px solid #ebeef5;
  display: block;
  height: 18px;
  margin: 0px 5px 0px 5px;
  padding: 5px 15px 5px 15px;
  border-radius: 4px;
  word-break: normal;
  width: auto;
  display: block;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-y: auto;
}
</style>


