import Vue from "vue";
import Router from "vue-router";
import Home from "@/pages/Home";
import FileBrowser from "@/pages/FileBrowser";
import QuickCommand from "@/pages/QuickCommand";
import Cockpit from "@/pages/Cockpit";
import Gadget from "@/pages/Gadget";
import Codec from "@/pages/tools/Codec";
import Json from "@/pages/tools/Json";
import Translate from "@/pages/tools/Translate";

Vue.use(Router);

// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      redirect: "/Gadget",
      component: Home,
      children: [
        {
          path: "/Cockpit",
          name: "Cockpit",
          component: Cockpit
        },
        {
          path: "/Gadget",
          name: "Gadget",
          component: Gadget,
          redirect: "/Gadget/Codec",
          children: [
            {
              path: "/Gadget/Codec",
              name: "Codec",
              component: Codec
            },
            {
              path: "/Gadget/Json",
              name: "Json",
              component: Json
            },
            {
              path: "/Gadget/Translate",
              name: "Translate",
              component: Translate
            }
          ]
        },
        {
          path: "/FileBrowser",
          name: "FileBrowser",
          component: FileBrowser
        },
        {
          path: "/QuickCommand",
          name: "QuickCommand",
          component: QuickCommand
        }
      ]
    }
  ]
});



